.logo {
  width: 100%;
  height: 94px;
  z-index: 100;

  display: flex;
  align-items: center;
  justify-content: center;

  box-shadow: 0px 4px 20px rgba(12, 36, 64, 0.1);
}

.logo__img {
  height: 62px;
  object-fit: contain;
  background-image: "../public/images/logo/logo.png";
  margin-right: 12px;
}

.logo__container img {
  height: 20.8px;
  margin: 6px;
}
